<template>
  <div class="map-container" id="map-container"></div>
</template>

<script>
export default {
  name: "mapView",
  components: {},
  data() {
    return {
      map: null,
      baseLayerUrl:'http://10.3.1.48:6080/arcgis/rest/services/test/testMap/MapServer/WMTS/tile/1.0.0/test_testMap/default/default028mm/{z}/{y}/{x}.png'
    };
  },
  mounted() {
    this.map = this.$utils.map.createMap("map-container",{attributionControl: false  });
    this.map.setView([-0.06754873618317839,  0.26822090148925787], 14);
    this.addBaseLayer()
    this.map.on('click', function(ev) {
    console.log(ev);
});
  },
  methods:{
    addBaseLayer(){
      this.$utils.map.createTileLayer(this.map, this.baseLayerUrl, {maxZoom: 18});
    }
  }
};
</script>

<style >
.map-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
</style>



